.header {
  background: #ffffff url("../img/bg_header.jpg") top no-repeat;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 50;

  &__bottom {
  }

  @media (max-width: $md3+px) {
    background: #ffffff url("../img/bg_header.jpg") 0 -20px no-repeat;
  }
}

.top-header {

  &__content {
    display: flex;
    align-items: center;
    height: 90px;


    @media (max-width: $md3+px) {
      height: 70px;
    }
  }

  &__column {
    flex: 0 1 50%;

    &:nth-child(2) {
      flex: 0 0 190px;
      text-align: center;
      @media (min-width: $md3+px) {
        align-self: flex-start;
        padding: 25px 0 0 0;
      }
      @media (max-width: $md3+px) {
        flex: 0 0 100px;
      }
    }
  }

  &__logo {
    position: relative;
    z-index: 5;
    display: inline-block;
    @media (max-width: $md3+px) {
      width: 100px;
      img {
        max-width: 100%;
      }
    }
    @media (max-width: 640px) {
      width: 90px;
    }
  }

  &__contacts {
    position: relative;
    z-index: 5;
  }
}

.menu {
  &__icon {

  }

  &__body {
    @media (max-width: $md2+px) {
      opacity: 0;
      display: none;
      position: fixed;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      visibility: hidden;
      overflow: auto;
      background-color: white;
      padding: 120px 20px 30px 20px;
      z-index: 2;
      transition: all .5s ease 5s;
      &:before {
        content: "";
        position: fixed;
        width: 100%;
        height: 105px;
        top: 0;
        left: 0;
        background-color: #1ab9ce;
        z-index: 3;
      }
      &._active {
        opacity: 1;
        display: block;
        visibility: visible;
      }
      @media (max-width: $md3+px) {
        padding: 90px 20px 30px 20px;
        &:before {
          height: 70px;
        }
      }
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    li {
      flex: 0 1 30%;
      margin: 5px 0;
    }

    @media (max-width: $md2+px) {
      display: block;
      li {
        margin: 0 0 20px 0;
      }
    }
  }

  &__link {
    font-family: "Play", sans-serif;
    font-weight: 700;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 27px;
    padding: 0 10px;
    border-radius: 50px;
    color: white;
    text-decoration: #80ccd6 underline;
    white-space: nowrap;
    @media (min-width: $md2+px) {
      &:hover {
        background-color: #068292;
      }
    }
    @media (max-width: $md2+px) {
      text-decoration: #000000 underline;
      color: black;
      padding: 0;
      font-size: 20px;
    }
  }
}


.contacts-header {
  display: flex;
  justify-content: center;
  @media (max-width: $md2+px) {
    justify-content: flex-end;
  }
  @media (max-width: 640px) {
    align-items: center;
  }

  &__column {
    padding: 0 30px;
    @media (min-width: $md2+px) {
      border-left: 1px solid #ffffff;
      &:first-child {
        border-left: none;
      }
    }
    @media (max-width: $md1+px) {
      padding: 0 15px;
    }
    @media (max-width: 640px) {
      padding: 0;
    }
  }

  &__item {
    &_icon {
      background: url("../img/ico/header/phone.png") left no-repeat;
      padding: 0 0 0 37px;
      @media (max-width: $md1+px) {
        padding: 0 0 0 25px;
        background-size: 15px;
      }

      @media (max-width: 640px) {
        background: none;
        padding: 0;
      }
    }

    &_second-phone {
      @media (max-width: $md2+px) {
        display: none;
      }
    }
  }

  &__label {
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    font-size: 12px;
    color: white;
    @media (max-width: 640px) {
      display: none;
    }
  }

  &__phone {
    color: white;
    font-size: 18px;
    font-weight: 900;
    white-space: nowrap;
    @media (max-width: $md1+px) {
      font-size: 16px;
    }
    @media (max-width: 640px) {
      display: block;
      background: url("../img/ico/header/phone.png") left no-repeat;
      width: 19px;
      height: 25px;
      font-size: 0;
      margin: 0 10px 0 0;
    }
  }
}

.bottom-header {
  padding: 10px 0;
  &__container {

  }

  &__row {

    display: flex;
    //height: 63px;
    align-items: center;
    justify-content: space-between;
    @media (min-width: $md1+px) {
      padding: 0 15px;
    }
  }

  &__column {
    flex: 0 1 calc(50% - 95px);
  }
}

.actions-header {
  display: flex;
  align-items: center;


  li {
    margin: 0 54px 0 0;
    @media (max-width: $maxWidthContainer+px) {
      margin: 0 30 / $maxWidthContainer * 100vw 0 0;
    }

    &:last-child {
      margin: 0;
    }
  }

  @media (max-width: 640px) {
    justify-content: space-around;
    //margin: 0 0 15px 0;
  }

  &__login {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    background: url("../img/ico/header/login.png") left no-repeat;
    display: inline-block;
    line-height: 24px;
    padding: 0 0 0 36px;
    color: #999999;

    span {
      border-bottom: 1px solid #999999;
    }

    @media (max-width: $md1+px) {
      padding: 0 0 0 27px;
    }

    @media (min-width: $md2+px) {
      &:hover {
        color: #1ab9ce;

        span {
          border-bottom: 1px dashed #1ab9ce;
        }
      }
    }
    @media (max-width: $md2+px) {
      padding: 0;
      background: none;
    }

  }

  &__checkin {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    background: url("../img/ico/header/user.png") left no-repeat;
    display: inline-block;
    line-height: 24px;
    padding: 0 0 0 33px;
    color: #999999;

    span {
      border-bottom: 1px solid #999999;
    }

    @media (max-width: $md1+px) {
      padding: 0 0 0 27px;
    }

    @media (min-width: $md2+px) {
      &:hover {
        color: #1ab9ce;

        span {
          border-bottom: 1px dashed #1ab9ce;
        }
      }
    }
    @media (max-width: $md2+px) {
      padding: 0;
      background: none;
    }
  }

  &__email {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    background: url("../img/ico/header/mail.png") left no-repeat;
    display: inline-block;
    line-height: 24px;
    padding: 0 0 0 35px;
    color: #999999;
    @media (max-width: $md2+px) {
      padding: 0;
      background: none;
    }

    span {
      border-bottom: 1px solid #999999;
    }

    @media (max-width: $md1+px) {
      padding: 0 0 0 27px;
    }
    @media (max-width: 640px) {
      padding: 0;
    }
    @media (min-width: $md2+px) {
      &:hover {
        color: #1ab9ce;

        span {
          border-bottom: 1px dashed #1ab9ce;
        }
      }
    }
  }
}

.info-header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: $md2+px) {
    justify-content: space-evenly;
  }

  &__column {
    padding: 0 30px 0 0;

    @media (max-width: $maxWidthContainer+px) {
      padding: 0 30 / $maxWidthContainer * 100vw 0 0;
    }

    &:last-child {
      padding: 0;
    }
  }

  &__callback {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    background: url("../img/ico/header/callback.png") left no-repeat;
    display: inline-block;
    line-height: 20px;
    padding: 0 0 0 32px;
    color: #1ab9ce;
    white-space: nowrap;

    span {
      border-bottom: 1px dashed #1ab9ce;
    }

    //@media (max-width: $md1+px) {
    //  background: none;
    //  padding: 0;
    //}
  }

  &__schedule {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    background: url("../img/ico/header/schedule.png") left no-repeat;
    display: inline-block;
    line-height: 18px;
    padding: 0 0 0 37px;
    color: #999999;

    @media (max-width: $md2+px) {
      display: none;
    }

    span {
      font-weight: 900;
    }

    @media (max-width: $md1+px) {
      background: none;
      padding: 0;
    }
  }

  &__cart {
    background: url("../img/ico/header/cart.png") 0 0 / 100% no-repeat;
    display: inline-block;
    width: 43px;
    height: 43px;
    position: relative;
    margin-right: 10px;

    span {
      line-height: 25px;
      padding: 0 11px;
      background-color: #f68038;
      border-radius: 15px;
      position: absolute;
      right: 0;
      top: -3px;
      transform: translate(70%, 0px);
      font-size: 18px;
      color: white;
      font-weight: 700;
    }

    @media (max-width: 640px) {
      width: 40px;
      height: 40px;
      span {
        font-size: 14px;
        line-height: 20px;
        padding: 0 7px;
        transform: translate(50%, 0px);
      }
    }
  }
}

.icon-menu {
  display: none;
  @media (max-width: $md2+px) {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5;
    span {
      transition: all .3s ease 0s;
      top: calc(50% - 1px);
      left: 0;
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: #ffffff;

      &:first-child {
        top: 0;
      }

      &:last-child {
        top: auto;
        bottom: 0;
      }
    }
    &._active {
      span {
        transform: scale(0);

        &:first-child {
          transform: rotate(-45deg);
          top: calc(50% - 1px);
        }

        &:last-child {
          transform: rotate(45deg);
          bottom: calc(50% - 1px);
        }
      }
    }
  }
}